<template>
  <div class="search">
    <van-sticky :offset-top="0">
      <HomeHeader :myBook="searchTitle" :isGoHome="isGoHome">{{
        goback
      }}</HomeHeader>
      <van-search
        v-model="value"
        show-action
        shape="round"
        placeholder="请输入搜索关键词"
        @search="onSearch"
        @input="onInput"
        :autofocus="true"
      >
        <template #action>
          <div>确定</div>
        </template>
      </van-search>
    </van-sticky>

    <div v-if="isShouBookItem">
      <BookItem
        v-for="(item, index) in books"
        :key="item.id"
        :item="item"
        :index="index"
      >
      </BookItem>
    </div>
    <div v-else class="keywords-list">
      <div v-if="isShowSuggest">暂无相应书籍，为您推荐以下书籍</div>
      <li
        v-for="(item, index) in keywords"
        :key="index"
        @click="onSearch(item)"
      >
        <img
          src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABUAAAAWBAMAAADdrcoLAAAAJFBMVEX///++vr7y8vLu7u7Dw8Pk5OTe3t719fXa2trs7OzPz8/Hx8dTXNRiAAAAP0lEQVQY02MgHXAIAoEYhM0YACRWQNmCIOAAYQcbGxubCkDYCkCCCcpWd3HxZsIq7smAJE42OxlhLxvCPYQBAECeCDq7BiEBAAAAAElFTkSuQmCC"
          alt=""
        /><span>{{ item }}</span>
      </li>
    </div>
    <!-- 可修改文案和加载图标的颜色 -->
    <van-loading color="#0094ff" v-if="isShouLoading" />
  </div>
</template>

<script>
import HomeHeader from "@/components/HomeHeader.vue";
import BookItem from "@/components/BookItem.vue";
// import  from "@/components/HomeHeader.vue";

export default {
  name: "Search",
  components: {
    HomeHeader,
    BookItem,
  },
  data() {
    return {
      value: "",
      isSearch: false,
      isGoHome: true,
      goback: "⮜ 返回",
      searchTitle: "搜索",
      books: [],
      keywords: [],
      isShouLoading: false,
      isShowSuggest: false,
      // isShouBookItem: true,
    };
  },
  created() {
    this.isSearch = false;
    if (!this.$route.query.isSearch) {
      // this.$nextTick(function () {
      //   let oInput = document.getElementsByClassName("van-field__control")[0];
      //   console.log(oInput); //输出：修改后的值
      //   oInput.autofocus = "autofocus";
      // });
    }
    if (this.$route.query.isSearch) {
      this.isSearch = this.$route.query.isSearch;
      this.value = this.$route.query.value;
      // console.log("search" + this.$route.query.value);
    }
  },
  computed: {
    isShouBookItem() {
      return this.isSearch;
    },
  },
  //路由更新直接搜索
  beforeRouteUpdate(to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    // console.log(to, from);
    // console.log(to.query.gender === this.gender);

    // console.dir(to, from, next);
    this.isSearch = to.query.isSearch;
    this.value = to.query.value;
    next();
  },
  //如果isSearch为true就搜索
  watch: {
    isSearch(n) {
      console.log(n);
      if (n == false) return;
      // console.log("xhr");
      this.onSearch();
    },
  },
  methods: {
    //搜索函数
    onSearch(value) {
      if (value) {
        let reg = /《(.+?)》/;
        // console.log(reg.test(value));
        // console.log(value.match(reg));
        if (reg.test(value)) {
          value = value.match(reg)[1];
        }
        this.value = value;
      }
      this.isShouLoading = true;

      this.$router.push({
        path: "/search",
        query: { isSearch: true, value: this.value },
        // component: Search,
      });
      if (this.value.trim() == "") {
        this.$toast("内容为空");
        this.isShouLoading = false;
        return;
      }
      this.axios
        .get("http://novel.kele8.cn/search?keyword=" + this.value)
        .then((res) => {
          console.log(res);
          if (!res.data.books.length) {
            this.isShowSuggest = true;
            this.isSearch = false;
            this.keywords = ["超级女婿", "最强兵王"];
          }
          this.books = res.data.books;
          this.isShouLoading = false;
        });
    },
    //发生改变就不显示内容
    onInput() {
      if (this.value == "") {
        this.isShowSuggest = false;
        this.isSearch = false;
        this.keywords = [];
        return;
      }
      this.isSearch = false;
      this.isShouLoading = true;
      this.axios
        .get("http://novel.kele8.cn/auto-complete?query=" + this.value)
        .then((res) => {
          console.log(res);
          this.keywords = res.data.keywords;
          if (!res.data.keywords.length) {
            this.isShowSuggest = true;
            this.keywords = ["超级女婿", "最强兵王"];
          }
          this.isShouLoading = false;
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
};
</script>

<style lang="less">
.search {
  // padding: 0 15px;
  .keywords-list {
    padding: 0 10px;
    div {
      padding: 0 5px;
      font-size: 14px;
    }
    img {
      padding: 0 5px 0 0;
    }
    li {
      display: flex;
      list-style: none;
      height: 25px;
      line-height: 25px;
      padding: 12px 0;
    }
  }
  .book-item .van-card__num {
    border: 0;
  }
}
</style>